import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";

import {
  showModal,
  showModalDataMarketPlcae,
  getDataMarketplace,
  hideModal,
} from "../../../actions/datamaster_action.jsx";
import { connect } from "react-redux";

import Tabel from "../../../components/helpers/tabel.jsx";
import ModalGlobal from "../../../components/helpers/ModalGlobal.jsx";
import HeadDataMarketPlcae from "./HeadDataMarketPlcae.jsx";
import { deleteData, postData, putData } from "../../../config/axios.jsx";
import { ToastNotification } from "../../../components/helpers/notification.jsx";
import { reset } from "redux-form";
import Swal from "sweetalert2";

const maptostate = (state) => {
  return {
    getDataMarketplace: state.datamaster.getDataMarketplace,
  };
};
const hapusData = (params, dispatch) => {
  Swal.fire({
    // title: "Anda Yakin !!",
    // text: "Ingin Menghapus Data Ini ?",
    html:
      "Apakah Anda Yakin Ingin " +
      "Menghapus " +
      "<h1><b>Kode Sales " +
      params.kode_marketplace +
      "</b> ?</h1>",
    icon: "warning",
    position: "top-center",
    cancelButtonText: "Tidak",
    showCancelButton: true,
    confirmButtonText: "OK",
    showConfirmButton: true,
  }).then((result) => {
    if (result.isConfirmed) {
      deleteData("marketplace/" + params._id)
        .then(() => {
          ToastNotification("success","Data Berhasil Di Hapus").then(() =>
            dispatch(getDataMarketplace())
          );
        })
        .catch((err) => {
          ToastNotification("error", 'Terjadi Kesalahan Saat Mengirim Data');
        });
    }
  });
};
class DataMareketPlace extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      isLoading: false,
      columns: [
        {
          dataField: "kode_marketplace",
          text: "Kode Marketplace",
          sort: true,
        },
        {
          dataField: "nama_marketplace",
          text: "Nama Marketplace",
        },

        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {
            let dataEdit = {
              id: row._id,
              kode_marketplace: row.kode_marketplace,
              nama_marketplace: row.nama_marketplace,
            };

            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    onClick={() => this.formEdit(dataEdit)}
                    className="btn btn-primary mr-3"
                  >
                    {" "}
                    <i className="fa fa-edit"></i>
                  </button>
                  &nbsp;
                  <button
                    onClick={() => hapusData(row,this.props.dispatch)}
                    className="btn btn-danger mr-3"
                  >
                    {" "}
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    this.props.dispatch(getDataMarketplace());
  }

  handleSubmit(data) {
    this.setState({
      isLoading: true,
    });
    let data_post = {
      kode_marketplace: data.kode_marketplace,
      nama_marketplace: data.nama_marketplace,
    };
    // console.log(data_post);
    this.state.isEdit
      ? putData("marketplace/" + data.id, {
          nama_marketplace: data.nama_marketplace,
        })
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => {
            ToastNotification("success", "Data Berhasil Disimpan");
          })
          .then(() => {
            this.props.dispatch(getDataMarketplace());
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .then(() => {
            this.props.dispatch(reset("HeadDataMarketPlcae"));
          })
          .catch((error) => {
            ToastNotification("info", error.response === undefined ? "Terjadi Kesalahan Saat Megirim Data " : error.response.data)
            this.setState({ isLoading: false });
          })
      : postData("marketplace", data_post)
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => {
            ToastNotification("success", "Data Berhasil Disimpan");
          })
          .then(() => {
            this.props.dispatch(getDataMarketplace());
          })
          .then(() => {
            this.props.dispatch(reset("HeadDataMarketPlcae"));
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .catch((error) => {
            ToastNotification("info", "Terjadi Kesalahan Saat Mengirim Data");
            this.setState({ isLoading: false });
          });
  }
  formEdit(dataEdit) {
    this.props.dispatch(showModal());
    this.props.dispatch(showModalDataMarketPlcae(dataEdit));
    this.setState({
      isEdit: true,
    });
  }
  showmodaltambah() {
    this.props.dispatch(showModal());
    this.props.dispatch(showModalDataMarketPlcae([]));
    this.setState({
      isEdit: false,
    });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Data Master</Link>
          </li>
          <li className="breadcrumb-item active">Data Marketplace</li>
        </ol>
        <h1 className="page-header">Data Marketplace </h1>
        <Panel>
          <PanelHeader>Data Marketplace</PanelHeader>
          <br />

          {/* Parameter Pint */}
          <div className="container">
            <Tabel
              keyField="kode_marketplace"
              exportCsv={true}
              tambahData={true}
              handleClick={() => this.showmodaltambah()}
              data={this.props.getDataMarketplace}
              columns={this.state.columns}
              empty={this.props.getDataMarketplace}
              textEmpty="Data Marketplace Kosong"
            />
          </div>
          <br />
          {/* End Master Sales */}
          <ModalGlobal
            size="P"
            title={
              this.state.isEdit
                ? "Edit Data Marketplace"
                : "Tambah Data Marketplace"
            }
            content={
              <HeadDataMarketPlcae
                isEdit={this.state.isEdit}
                isLoading={this.state.isLoading}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            }
          />

          {/* <Modal  title={ this.state.isEdit ? 'Edit Parameter HargaEmas' : 'Tambah Parameter HargaEmas' } content={ <FormModalKategori isEdit={this.state.isEdit} /> } isOpen={ this.props.hideModal} onSubmit={ (data)=>this.handleSubmit(data) } /> */}

          {/* End Tambah Master Sales  */}
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate, null)(DataMareketPlace);
