import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { hideModal } from "../../../actions/datamaster_action";

import { HiidenFiled, ReanderField } from "../../../components/helpers/field";
// import { NumberOnly } from "../../../Validasi/normalize";
// import ValidasiMasterJenis from "../../../Validasi/ValidasiMasterJenis.jsx";
import Loading from "react-fullscreen-loading";

const maptostate = (state) => {
  if (state.datamaster.showModalMarketPlace !== null) {
    return {
      initialValues: {
        id:
          state.datamaster.showModalMarketPlace.id,
        kode_marketplace:
          state.datamaster.showModalMarketPlace.kode_marketplace,
        nama_marketplace:
          state.datamaster.showModalMarketPlace.nama_marketplace,
      },
    };
  }
};

class HeadDataMarketPlcae extends Component {
  constructor(props) {
    super(props);
    this.state = {
      aktif: false,
    };
  }

  componentDidMount() {
    setTimeout(() => {
      this.props.isEdit ? document.getElementById("nama_marketplace").focus() :
        document.getElementById("kode_marketplace").focus()
    }, 100);
  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        autoComplete="off"
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
      >
         {
          this.props.isEdit ? <>
             <Field
              name="id"
              component={HiidenFiled}
              type="hidden"
              readOnly={this.props.isEdit}
            />
          </> : null
        }
        <div className="form-group">
          <Field
            tabIndex="1"
            id="kode_marketplace"
            label="Kode Marketplcae"
            name="kode_marketplace"
            readOnly={this.props.isEdit}
            placeholder={"Silahkan Masukan Kode Marketplcae"}
            component={ReanderField}
          />
        </div>
        <div className="form-group">
          <Field
            tabIndex="2"
            id="nama_marketplace"
            label="Nama Marketplcae"
            name="nama_marketplace"
            placeholder={"Silahkan Masukan Nama Marketplcae"}
            onBlur={() => this.setState({ aktif: true })}
            component={ReanderField}
          />
        </div>

        <div className="row">
          <div className="col-12 text-right">
            <button
              className="btn btn-white"
              onClick={() => this.props.dispatch(hideModal())}
              type="button"
              disabled={this.props.isLoading}
            >
              Batal
            </button>
            &nbsp;
            <button
              tabIndex="3"
              className={this.state.aktif ? "btn btn-primary" : "btn btn-white"}
              disabled={this.props.isLoading}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
          </div>
        </div>
        {this.props.isLoading ? (
          <Loading
            loading
            background="rgba(0, 0, 0, 0.35)"
            loaderColor="rgba(94, 147, 117, 1)"
          />
        ) : null}
      </form>
    );
  }
}

HeadDataMarketPlcae = reduxForm({
  form: "HeadDataMarketPlcae",
  enableReinitialize: true,
  //   validate: ValidasiMasterJenis,
})(HeadDataMarketPlcae);
export default connect(maptostate, null)(HeadDataMarketPlcae);
